import React from "react";

const RaiseDemand = () => {
  return (
    <>
      <div class="container bg-white p-2 rounded-2">
        <form class="row px-4 py-4 mx-2 my-2 justify-content-center shadow-lg">
          <h3 class="text-alternate text-primary">Raise Demand</h3>
          <hr />
          <div class="col-md-6 col-12 mb-2">
            <p class="text-alternate">Select Project</p>
            <div class="input-group">
              <select class="form-control" id="project" name="Project">
                <option>Select Project</option>
              </select>
            </div>
          </div>
          <div class="col-md-6 col-12 mb-2">
            <p class="text-alternate">Select Building</p>
            <div class="input-group">
              <select class="form-control" id="building" name="building">
                <option>Loading...</option>
              </select>
            </div>
          </div>
          <div class="col-md-8 col-12 mb-2">
            <p class="text-alternate">Select Payment Plan</p>
            <div class="input-group">
              <select class="form-control" id="paymentPlan" name="paymentPlan">
                <option>Payment Plan 1</option>
                <option>Payment Plan 2</option>
                <option>Payment Plan 3</option>
              </select>
            </div>
          </div>
          <div class="col-md-4 col-12 mb-2">
            <p class="text-alternate">Raise Demands For this Plan</p>
            <button type="button" class="btn btn-success">
              Set Plan Active
            </button>
          </div>
        </form>
      </div>
    </>
  );
};

export default RaiseDemand;
